	module.exports = {"data":{"markdownRemark":{"html":"<p>Yesterday Nathan Fillion <a href=\"http://twitter.com/NathanFillion/statuses/106853202909073409\">tweeted</a> about finding a permutation of his name on a website. At first I just chuckled, but it soon occurred to me that this would be a fun problem to solve with scripting.</p>\n<p>So I whipped up <a href=\"https://gist.github.com/1174851\">fillion.rb</a> to peruse a webpage for permutations of a given word. It was fun to figure out a quick algorithm to find permutations, and then fight scope creep (I ended up writing a crawler for an entire domain).</p>\n<p>The beauty of development is being able to look for a challenge anywhere.</p>","fields":{"post":{"title":"fillion.rb","link":"https://gist.github.com/1174851","publishedAtTimestamp":null,"publishedAt":null}}}},"pathContext":{"slug":"/fillion-rb.html"}}

